.home-screen{
  min-height: 80vh;
}

.form-container{
  background: #fff;
  box-shadow: 0px 0px 9px 0px #CBCACB;
  font-family: 'Open Sans', sans-serif;
  padding: 30px;
}

.form-screen{
  max-width: 452px;
}

.card-operation{
  min-width: 230px;
  min-height:120px;
}

.input-operation:disabled{
  background: none;
  border: none;
  outline:0;
}

.footer { 
  bottom: 0;
  padding-top: 3em;
  position: relative;
  font-family: 'Open Sans', sans-serif;
  width: 100%;
}

.delete-icon {
  cursor: pointer;
} 

.edit-icon {
  cursor: pointer;
}

.nav-mobile{
  align-items: center;
  display: flex;
  height: 60px;
  justify-content: start;
}

.menu-bars{ 
  color: white;   
  background: none;
  font-size: 25px;
  margin-left: 10px;
  margin-right: 10px;
}

.menu-bars:hover {
  color: grey;
}

.nav-menu {    
  display: flex;
  height: 100vh;  
  justify-content: start; 
  top: 0;
  left: -100%;
  position: fixed;
  width: 210px;
}

.nav-menu.active {
  left: 0;
  transition: 350ms;
  z-index: 100;
}

.nav-menu-items {
width: 100%;
}

.navbar-toggle {
  align-items: start;
  display: flex;  
  height: 80px;
  justify-content: center;
  margin-top: 45px;
  margin-left: 0;
  width: 100%;
}

.nav-text {
  align-items: center; 
  color:white;
  display: flex;
  font-size: 20px;
  height: 60px;    
  justify-content: start;
  list-style: none;
  padding: 8px 0px 8px 0px;
  text-decoration: none;
}

.nav-text:hover {
  color: grey;
  text-decoration: none;
}

